@font-face {
  font-family: "Ubuntu Mono";
  src: url("fonts/UbuntuMono-Regular.ttf") format("truetype");
}
@font-face {
  font-family: "Ubuntu Mono Italic";
  src: url("fonts/UbuntuMono-Italic.ttf") format("truetype");
}
body {
  background-color: gray;
}

.container {
  font-family: "Ubuntu Mono";
  display: grid;
  margin: 0 auto;
  max-width: 1200px;
}
.container__p {
  font-family: "Ubuntu Mono Italic";
}
.container__link {
  color: beige;
}